.App {
  text-align: center;
  font-family: 'Arial', sans-serif;
  width: 100%;
}

.container {
  background: linear-gradient(90.05deg, #f1da57 .05%, #dc9f62 99.96%);
}

.container .first-m {
  display: flex;
  justify-content: center;
}

.container .first-m .lama {
  position: relative;
  width: 100%;
}

.line {
  height: auto;       /* 保持图片的纵横比 */
  display: block;     /* 去除底部空隙 */
}

.mint-times {
  color: #f0553a;
}

.header {
  background-color: #f5f5f5;
  width: 100%;
  padding: 20px;
}

.top-bar {
  display: flex;
  flex-wrap: wrap;    /* 允许换行以适应小屏幕 */
  width: 90%;
  justify-content: space-between;
  align-items: center;
  padding: 20px;      /* 修改为适合小屏幕的padding */
}

.logo {
  width: 15rem;
}

.middle-empty {
  flex-grow: 1;
}

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.buy-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.buy-button {
  padding: 10px 20px;
  background-color: #008CBA;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

.cool-section {
  display: flex;
  justify-content: center;
  align-items: center;
}

.container .first-top {
  display: flex;
  flex-direction: column; /* 使其在小屏幕上垂直排列 */
  align-items: center;    /* 中心对齐 */
  padding: 2rem;         /* 增加一些padding */
}

.connect-btn {
  font-size: 2rem;
  background-color: #fff;
  padding: 1rem 2rem;
  border-radius: 10rem;
  border: 2px solid #000;
  color: #000;
  cursor: pointer;
  position: relative;
  display: flex;
  align-items: center;
}

.container .first-top .connect-btn img {
  width: 34rem;
  margin-right: 8rem;
}

.container .first-top .connect-btn .popover {
  position: absolute;
  left: 0;
  top: 110rem;
  padding-top: 10px;
  background-color: #fff;
  border-radius: 24rem;
  box-shadow: 0 0 4rem 8rem #0004;
  cursor: default;
}

.container .first-top .connect-btn .popover .address {
  font-size: 36rem;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10rem 20rem;
}

.container .first-top .connect-btn .popover .address svg {
  cursor: pointer;
  margin-left: 10rem;
}

.container .first-top .connect-btn .popover .logout {
  border-top: 1px solid #000;
  text-align: center;
  padding: 16rem;
}

.mint-btn {
  font-size: 2rem;
  background-color: #fff;
  padding: 1rem 2rem;
  border-radius: 10rem;
  border: 2px solid #000;
  color: #000;
  cursor: pointer;
  position: relative;
  display: flex;
  align-items: center;
}

.top-bar {
  display: flex;
  justify-content: center; /* 在主轴方向上居中对齐 */
  align-items: center;     /* 在交叉轴方向上垂直居中 */
  width: 100%;            /* 让 top-bar 占满整个宽度 */
  padding: 20px;          /* 适合小屏幕的padding */
}

.top-bar-content {
  display: flex;
  justify-content: space-between; /* 在行内均匀分布 logo 和按钮 */
  align-items: center;            /* 垂直居中 */
  width: 90%;                     /* 设置宽度为 90% */
  max-width: 1200px;             /* 可选：限制最大宽度 */
  padding-top: 40px;
}

.logo {
  width: 15rem; /* 或其他合适的宽度 */
}

.connect-btn {
  font-size: 2rem;
  background-color: #fff;
  padding: 1rem 2rem;
  border-radius: 10rem;
  border: 2px solid #000;
  color: #000;
  cursor: pointer;
  margin-left: auto; /* 将按钮推到右侧 */
}

.social{
  padding:1rem
}


/* 媒体查询 */
@media (max-width: 768px) { /* 平板和手机屏幕 */
  .logo {
    width: 10rem; /* 小屏幕上缩小logo */
}

.top-bar {
    padding: 10px; /* 减小padding以适应小屏幕 */
}

.connect-btn {
    font-size: 1.5rem; /* 调整按钮字体大小 */
    padding: 0.5rem 1rem; /* 调整按钮的padding */
}

  .first-m {
      flex-direction: column; /* 小屏幕上竖向排列 */
  }

  .first-m .lama img {
      width: 100%; /* 确保图像在小屏幕上自适应宽度 */
      height: auto; /* 保持比例 */
  }

  .mint-times {
      font-size: 1.5rem; /* 调整字体大小 */
  }

  .mint-btn {
      font-size: 1.5rem; /* 调整按钮字体大小 */
      padding: 0.5rem 1rem; /* 调整按钮的padding */
  }

  .line {
      width: 100%; /* 使line在小屏幕上宽度100% */
  }
}
